.card-wrapper {
  display: flex;
  width: 150px;
  height: 150px;
  flex-direction: column;
  background-color: white;
  margin: 10px;
  gap: 15px;
  border-radius: 5px;
  justify-content: center;
  align-items: center;

  .card-img {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  p {
    color: black;
    margin: 3px;
    font-size: 12px;
  }
}

// アンドロイド対応
@media (max-height: 760px) {
  .card-wrapper {
    height: 135px;
    width: 135px;
  }
}
